import config from "../../config.js"
export default {
	data() {
		return {
			user: {
				id: "LittleRedHat",
				token: "",
			},
			message: "",
			latitude: 22.93300517699429,//39.909,
			longitude: 113.382958850984,//116.39742,

			title: 'Hello',

			markers: [],
			bottomMsg: "动一动地图鸭",
		}
	},
	onReady() {
		let that = this;
		// https://uniapp.dcloud.io/api/location/map?id=createmapcontext
		// 据说可以获得当前位置
		// 但是我调用了没反应
		// 你们可以看看是有什么条件还是什么情况
		uni.getLocation({
			type: 'gcj02', //返回可以用于uni.openLocation的经纬度
			success: function(res) {
				that.latitude = res.latitude;
				that.longitude = res.longitude;
				that.debugMsg = "点击图钉或气泡发表你的评论";
				that.updateMsg();
			}
		});
	},
	onLoad() {
		
	},
	methods: {
		getTextWidth(text){
			for(let i = 0;i<text.length;i++){
				
			}
		},
		updateMsg() {
			let data = {
				userId: "Hello",
				loc: {
					log: this.longitude,
					lat: this.latitude
				},
				period: {
					start: -1000 * 60 * 60 * 24,
					end: 0
				},
				range: 0.01,
			}
			const that = this;
			uni.request({
				url: `${config.HOST}/${config.API.MESSAGE}/get`,
				method: "post",
				dataType: "json",
				data: data,
				success(res) {
					const {
						data
					} = res;

					if (!data.ok) {
						return;
					}

					const {
						results
					} = data;

					const curMark = {
						id: 0,
						latitude: that.latitude,
						longitude: that.longitude,
						iconPath: '', //显示的图标        
						rotate: 0, // 旋转度数
						width: 50,
						height: 50,
						alpha: 0.5 //透明度
					};

					let marks = [];
					
					marks.push({
							id: 0,
							latitude: that.latitude,
							longitude: that.longitude,
							iconPath: '',
							width: 50,
							height: 50,
							content: 'None',
						});
						
					for (let i = 0; i < results.length; i++) {
						const msg = results[i];
						marks.push({
							id: i + 1,
							latitude: msg.loc.y,
							longitude: msg.loc.x,
							iconPath: '../../static/talk_bubble6.png',
							width: 50,
							height: 50,
							content: msg.msg,
						});
					}




					
					that.markers = marks;
					console.log(marks);
				},
				fail(err) {
					uni.showToast({
						title: "消息获取失败",
						icon: "none"
					})
				}
			})
		},
		clickMap(e) {
			this.getCenter();
		},
		getCenter() {
			// https://uniapp.dcloud.io/api/location/map?id=createmapcontext
			const appMap = uni.createMapContext("map-body", this);
			const that = this;

			appMap.getCenterLocation({
				success(res) {
					const {
						latitude,
						longitude
					} = res;
					that.latitude = latitude;
					that.longitude = longitude;
					that.bottomMsg = "点击图钉或气泡发表你的评论"
					that.updateMsg();
				},
				fail(err) {
					console.log(err);
				}
			});
		},

		clickMarker(e) {
			// let id = e.detail.markerId
			// let id = e.detail.markerId
			let id = e.markerId
			const that = this;
			console.log(id);
			uni.setStorageSync('storage_key_latitude', that.markers[id].latitude);
			uni.setStorageSync('storage_key_longitude', that.markers[id].longitude);
			uni.navigateTo({
				url: '../add/add'
			});
			this.getCenter();
			
		},
		sendMessage() {
			if (this.isSending) {
				return uni.showToast({
					title: "正在发送，请稍后",
					icon: "none"
				})
			}

			if (!this.message) {
				return uni.showToast({
					title: "内容不能为空",
					icon: "none"
				})
			}

			const that = this;
			this.isSending = true
			let data = {
				userId: "Hello",
				loc: {
					log: this.longitude,
					lat: this.latitude
				},
				msg: this.message,
			}

			uni.request({
				url: `${config.HOST}/${config.API.MESSAGE}/send`,
				method: "post",
				dataType: "json",
				data: data,
				success(data) {
					that.message = ""
					that.isSending = false
				},
				fail(err) {
					that.isSending = false
					uni.showToast({
						title: "发送失败，请稍后再试",
						icon: "none"
					})
				}
			})
		}
	}
}
